/* eslint-disable */

import GridIterator from './grid-iterator';

export default class ComplexGridIterator extends GridIterator {
  constructor(width, height, realMin, imagMin, realMax, imagMax, component) {
    super(width, height, component);
    this.realMin = realMin;
    this.imagMin = imagMin;
    this.realMax = realMax;
    this.imagMax = imagMax;
  }
    
  each(callback) {
    return GridIterator.prototype.each.call(this, (point) => {
      callback(this.toComplex(point.x, point.y));
    });
  }

  toComplex(x, y) {
    const r = (x / this.width) * (this.realMax - this.realMin * 1) + this.realMin * 1;
    const i = (y / this.height) * (this.imagMax - this.imagMin) + this.imagMin;
    
    return { x, y, r, i }
  }
}



