/* eslint-disable */

import { constantly } from './constantly';

export default class GridIterator {
  constructor(width, height, component) {
    this.width = width;
    this.height = height;
    this.current = null;
    this.component = component;
  }
  
  destroy() {
    if (this.constantly) {
      this.constantly.destroy();
      this.constantly = null;
    }
  }

  next() {
    if (!this.current) {
      this.current = { x: 0, y: 0 };
    } else {
      if (this.current.x == this.width) {
        this.current.x = 0;
        this.current.y++;
      } else if (this.current.y == this.height) {
        this.current = null;
      } else {
        this.current.x++;
      }
    }
    return this.current;
  }
  
  each(callback) {
    this.constantly = constantly(() => {
      const point = this.next();
      if (!point) this.destroy();
      else callback(point);    
    });
  }
}



