<template>
  <section>
    <h1>Mandelbrot <small>(and Julia)</small></h1>
    <p>A html canvas showing a Mandelbrot set, and some options to show a Julia set.</p>
    <p>Click/shift to zoom in and out, or pinch-in/out on touchscreen.</p>
    <v-touch
      class="touch-area"
      @pinchend="pinch">
      <figure>
        <canvas
          class="mandelbrot-canvas"
          width="100"
          height="100"
          @click="clickZoom" />
      </figure>
    </v-touch>

    <div class="container">
      <div>
        <button
          class="btn"
          @click="snapshot">Snapshot</button>
        <button
          class="btn"
          @click="reset">Reset</button>
        <button
          class="btn btn-primary"
          @click="update">Update</button>
        <button
          class="btn"
          @click="openFullscreen">Fullscreen</button>
        <label
          v-if="isHiResAvailable"
          for="hiRes">
          <input
            id="hiRes"
            v-model="hiRes"
            type="checkbox"
            @click="update">
          hi-res
        </label>
      </div>

      <div class="columns">
        <div class="column form-group col-2 col-md-6 my-1">
          <label
            class="form-label"
            for="realMin">R min</label>
          <input
            id="realMin"
            v-model.number="realMin"
            class="form-input"
            type="number"
            step="any">
        </div>
        <div class="column form-group col-2 col-md-6 my-1">
          <label
            class="form-label"
            for="realMax">R max</label>
          <input
            id="realMax"
            v-model.number="realMax"
            class="form-input"
            type="number"
            step="any">
        </div>
        <div class="column form-group col-2 col-md-6 my-1">
          <label
            class="form-label"
            for="imagMin">i min</label>
          <input
            id="imagMin"
            v-model.number="imagMin"
            class="form-input"
            type="number"
            step="any">
        </div>
        <div class="column form-group col-2 col-md-6 my-1">
          <label
            class="form-label"
            for="imagMax">i max</label>
          <input
            id="imagMax"
            v-model.number="imagMax"
            class="form-input"
            type="number"
            step="any">
        </div>
        <div class="column form-group col-2 col-md-6 my-1">
          <label
            class="form-label"
            for="maxIterations">max depth</label>
          <input
            id="maxIterations"
            v-model="maxIterations"
            class="form-input"
            type="number">
        </div>
      </div>

      <div class="columns">
        <div class="column form-group col-2 col-md-12 my-1">
          <label
            for="julia"
            class="form-switch">
            <input
              id="julia"
              v-model="julia"
              type="checkbox"
              @click="update">
            <i class="form-icon"/> Julia
          </label>
        </div>
        <div class="column form-group col-2 col-md-6 my-1">
          <label
            class="form-label"
            for="juliaR">Real</label>
          <input
            id="juliaR"
            v-model.number="juliaR"
            class="form-input"
            type="number"
            step="any" >
        </div>
        <div class="column form-group col-2 col-md-6 my-1">
          <label
            class="form-label"
            for="juliaI">Imaginary</label>
          <input
            id="juliaI"
            v-model.number="juliaI"
            class="form-input"
            type="number"
            step="any" >
        </div>
      </div>
    </div>

    <div class="snapshots">
      <div
        v-for="snapshot in snapshots"
        :key="snapshot"
        class="snapshot">
        <img
          :src="snapshot"
          :key="snapshot" >
      </div>
    </div>
  </section>
</template>

<script>
/* eslint-disable */
import CanvasWriter from "../assets/canvas-writer";
import imageBufferManipulate from "../assets/image-buffer-manipulate";
import ComplexGrid from "../assets/complex-grid-iterator";
import canvasColor from "../assets/canvas-color";
import fullscreen from "fullscreen";
import { getElementScreenOffset } from "../assets/get-element-offset";
import { getDevicePixelRatio } from "../assets/get-device-pixel-ratio";

export default {
  name: "Mandelbrot",
  data() {
    return {
      redrawRate: 20,
      realMin: -2.5,
      realMax: 1.5,
      imagMin: -2,
      imagMax: 2,
      hiRes: false,
      maxIterations: 50,
      julia: false,
      juliaR: -1,
      juliaI: 0,
      snapshots: []
    };
  },
  mounted() {
    this.update();
  },
  beforeDestroy() {
    this.destroyChildObjects();
  },
  computed: {
    isHiResAvailable() {
      return getDevicePixelRatio() !== 1;
    }
  },
  methods: {
    snapshot() {
      this.snapshots.push(this.canvasWriter.toCanvas().toDataURL());
    },
    reset() {
      this.realMin = -2.5;
      this.realMax = 1.5;
      this.imagMin = -2;
      this.imagMax = 2;
      this.julia = false;
      this.juliaR = -1;
      this.juliaI = 0;
      this.hiRes = false;
      this.maxIterations = 50;
      this.update();
    },
    pinch(event) {
      const canvasDevicePos = getElementScreenOffset(event.target);
      const canvasPos = this.canvasWriter.getCanvasPoint(
        event.center.x - canvasDevicePos.x,
        event.center.y - canvasDevicePos.y
      );
      const point = this.grid.toComplex(canvasPos.x, canvasPos.y);
      const scale = event.additionalEvent === "pinchin" ? 0.5 : 2;
      this.zoom(point, scale);
    },
    clickZoom(event) {
      const canvasPos = this.canvasWriter.getCanvasPoint(
        event.offsetX,
        event.offsetY
      );
      const point = this.grid.toComplex(canvasPos.x, canvasPos.y);
      const scale = event.shiftKey ? 0.5 : 2;
      this.zoom(point, scale);
    },
    openFullscreen(event) {
      const onAttain = () => {
        const canvas = document.getElementsByClassName("mandelbrot-canvas")[0];
        canvas.style.width = screen.width + "px";
        canvas.style.height = screen.height + "px";
        this.update();
      };

      const onRelease = () => {
        const canvas = document.getElementsByClassName("mandelbrot-canvas")[0];
        canvas.style.width = "";
        canvas.style.height = "";
        this.update();
        this.fullscreen.dispose();
        this.fullscreen = null;
      };

      if (this.fullscreen) this.fullscreen.dispose();
      const canvas = document.getElementsByClassName("mandelbrot-canvas")[0];
      this.fullscreen = fullscreen(canvas);
      this.fullscreen.on("attain", onAttain);
      this.fullscreen.on("release", onRelease);
      this.fullscreen.on("error", () => {
        console.error("fullscreen not supported");
      });
      this.fullscreen.request();
    },
    zoom(point, scale) {
      const width = this.grid.realMax - this.grid.realMin;
      const height = this.grid.imagMax - this.grid.imagMin;
      this.realMin = point.r - width / 2 / scale;
      this.realMax = point.r + width / 2 / scale;
      this.imagMin = point.i - height / 2 / scale;
      this.imagMax = point.i + height / 2 / scale;
      this.update();
    },
    destroyChildObjects() {
      if (this.canvasWriter) this.canvasWriter.destroy();
      if (this.grid) this.grid.destroy();
      if (this.fullscreen) this.fullscreen.dispose();
    },
    update() {
      if (this.grid) this.grid.destroy();

      const canvas = document.getElementsByClassName("mandelbrot-canvas")[0];
      if (typeof jest !== "undefined") return;

      if (!this.canvasWriter) {
        this.canvasWriter = new CanvasWriter(canvas, undefined, this.hiRes);
        this.canvasWriter = Object.assign(
          this.canvasWriter,
          imageBufferManipulate
        );
      } else {
        this.canvasWriter.hiRes = this.hiRes;
        this.canvasWriter.refresh();
      }

      this.height = this.canvasWriter.height;
      this.width = this.canvasWriter.width;

      this.grid = new ComplexGrid(
        this.width,
        this.height,
        this.realMin,
        this.imagMin,
        this.realMax,
        this.imagMax,
        this
      );

      this.grid.each(point => {
        const result = this.calculate(point);
        this.setPointColor(point, result);
      });
    },
    calculate(c) {
      let value;
      let iterations = 0;
      let z = { r: c.r, i: c.i };
      for (; iterations < this.maxIterations; iterations++) {
        let r = this.julia ? this.juliaR : c.r;
        let i = this.julia ? this.juliaI : c.i;
        z = {
          r: z.r * z.r - z.i * z.i + r,
          i: z.r * z.i * 2 + i
        };

        value = z.r * z.r + z.i * z.i;
        if (value > 4) break;
      }

      return { iterations, value, z, c };
    },
    setPointColor(point, result) {
      const r = ~~(result.value * 6);
      const g = result.value < 9 ? 0 : ~~(result.value * 20);
      const b = 255 - result.iterations / this.maxIterations * 255;

      const color = canvasColor.fromRGB(r, g, b);
      this.canvasWriter.setPoint(point.x, point.y, color);
    }
  }
};
</script>

<style lang="scss" scoped>
.touch-area {
  display: inline-block;
}

canvas {
  border: 1px solid shade(0.8);
  width: 500px;
}

.snapshots {
  line-height: 0;
  .snapshot {
    display: inline-block;
    width: 24%;
    margin: 0.5%;
    img {
      width: 100%;
    }
  }
}
</style>



